import {PairType, responseType} from "../Types/commonTypes";

type myResultType = {
    rates: { //   rates
        item: Array<PairType> //  item   
    }
}

export const EditDataFromServer = (response: responseType) => {
    const convert = require("xml-js"); //   json
    const data: myResultType = JSON.parse(
        convert.xml2json(response.data, {compact: true, spaces: 2})
    );
//   JSON    ,     -  
    const response2: Array<PairType> = [] //      
    data.rates.item.forEach((r: object) => { //    
        const myObject: any = {}; // ,         
        Object.keys(r).forEach((o, index2) => { //    
            const myKey = o.toUpperCase() //    
            const myValue = Object.values(r)[index2]._text //    
            if (!isNaN(myValue)) { //     
                myObject[myKey] = Number(myValue) //       
                return //      
            }
            if (myKey && myValue) { //      
                myObject[myKey] = myValue //       
            }
        })
        if (myObject) { //   
            response2.push(myObject) //     
        }
    })
    return response2 //           
}
